
:root{
    --container: 968px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.container{
    max-width: 760px;
    margin-left: auto;
    margin-right: auto;
}

body{
    margin: 0;
    background-color: #9393bb4d;
}

.nav {
    display: flex;
    justify-content: flex-end;
}

.p-1{
    padding: 0.5rem;
}

.nav__menu{
    padding: 10px 0;
    border-radius: 10px;
    background: #1a152f0d;
    width: max-content;
    margin: auto;
}


.icon-pack{
    padding: 5px;
    border-radius: 5px;
    background-color: #fff;
    width: max-content;
    display: flex;
    align-content: center;
}

.w-100{
    width: 100%;
}

.nav__list{
    width: 100%;
    list-style: none;
    margin: 0;
    display: flex;
    padding: 0px;
    background-color: #dedeea;
}

.nav{
}

.pb-2{
    padding-bottom: 1.725rem;
}

.nav__link:hover{
    transition: .4s;
    background-color: #1a152f1c;
}


.heading{
    font-size: 3.4rem;
    margin: 0;
}

button{
    transition: .3s;
}
button[disabled]{
    transition: .3s;
    color: gainsboro !important;
    background-color: white !important;
}

image{
    filter: grayscale(100%);
}

.nav-btn-close{
    border: none;
    border-radius: 5px;
    font-size: 25px;
    font-weight: 900;
    background-color: #000;
    color: white;
    padding: 2.5px 10px;
    margin-bottom: 10px;
}

.nav__link{
    margin: 10px 0;
    font-size: 13px;
    font-weight: 400;
    justify-content: space-between;
    text-decoration: none;
    align-content: center;
    display: flex;
    background-color: #1a152f0d;
    padding: 10px 20px;
    border-radius: 2px;
    color: #000;
}

.rounded{
    border-radius: 5px;
}

.mr-1{
    margin-right: 10px;
}

.bg-white{
    background-color: #fff;
}

.footer ul {
    padding: 0px;
}

.alert{
    position: fixed;
    bottom: 20px;
    left: 45vw;
    background-color: black;
    color: #fff;
    padding: 10px 15px;
    border-radius: 10px;
    opacity: .7;
    z-index: 20;
}

.footer ul li{
    cursor: pointer;
}

.nav__link h3{
    align-self: center;
    margin: 0px 7px !important;
}

hr.sep{
    border-bottom: none;
    border-color: #05053142;
}

.t-center{
    text-align: center;
}

.contacts{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    width: 100%;
    list-style: none;
    padding: 0;
}

.heading_1{
    font-size: 6.4rem;
}

.p-2{
    padding: 1.25rem;
}

.m-0{
    margin: 0;
}

.m-auto{
    margin: auto;
}


.m-h-auto{
    margin: unset auto;
}

.m-v-auto{
    margin: auto unset;
}

.title{
    padding: 15px;
    border-radius: 10px;
    background-color: #1a152f0d;
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.title h2{
    margin: 0;
}


.mt-2{
    margin-top: 1.725em;
}

.mb-2{
    margin-bottom: 1.725em;
}

.w-fit-content{
    width: fit-content;
}

.title > h4 {
    color: #484848;
    margin: 0;
}

.content{
    margin-top: 1rem;
    padding: 0.5rem;
    height: calc(100vh - 200px);
    background-color: #1a152f0d;
    border-radius: 10px;
    overflow-y: auto;
}

.nav__link.active{
    background-color: black !important;
    color: white !important;
}

.heading_2{
    font-size: 4.5rem;
}

.small{
    font-size: 14px;
}

.skills-list{
    display: flex;
    flex-wrap: nowrap;
    overflow-x: auto;
    justify-content: flex-start;
    padding:  10px 0;
}

.skill-item{
    transition: .4s;
    padding: 10px;
    border-radius: 7px;
    background-color: #bdc1c6a3;
    color: black;
    margin: 15px 0px;
    cursor: pointer;
}

.skill-item.active{
    transition: .4s;
}

.w-100{
    width: 100%;
}

.text-center{
    text-align: center;
}

.d-flex.text-center{
    justify-content: center;
}

.bold{
    font-weight: 700;
}

.nav-btn{
    cursor: pointer;
    border: 0;
    padding: 5px 10px;
    border-radius: 7px;
    background: #9795942b;
    transition: .24s;
    box-shadow: 0px 1px 1px -1px #332d2d;
}

.nav-btn:active{
    box-shadow: inset 0px 0px 1px #00000075;
    transition: .24s;
}

.btn{
    padding: 10px;
    border: 0;
    border-radius: 10px;
    background: #7b432f2e;
    box-shadow: 0px 2px 4px -2px #332d2d;
    transition: .4s;
}

.icon{
    width: 30px;
}

p{
    margin: 5px 0;
}

.p{
    background-color: aliceblue;
    border-radius: 5px;
    padding: 5px 10px;
    line-height: 1.725;
}

.w-auto{
    width: auto;
}

.btn:hover{
    background-color: black;
    color: #fff;
    transition: .4s;

}

.no-decor{
    text-decoration: none;
    color: inherit;
}

.d-flex{
    display: flex;
}

.align-center{
    align-items: center;
}


.contact{
    width: max-content;
    height: max-content;
    border: 2px solid #476080a3;
    text-align: center;
    padding: 5px;
    margin: 5px;
    display: flex;
    border-radius: 5px;
    align-items: center;
    justify-content: center;
}

.image{
    height: 200px;
    width: 200px;
}

.image > img{
    width: 100%;
    height: 100%;
    border-radius: 10px;
}

.row{
    display: flex;
    justify-content: flex-start;
    flex-wrap:  wrap;
}

.row .icon-pack:not(:last-child){
    margin-right: 5px;
}

.row .icon-pack{
    margin-top: 10px;
}

.bold{
    font-weight: bold;
}

.contact-me{
    width: 100%;
    display: block;
}

.portfolio{
    display: block;
    margin-top: 10px;
    background-color: #fff;
    border-radius: 10px;
    padding: 5px;
}

.p-header{
    height: 200px;
    margin-bottom: 10px;
    background-size: contain;
    background-repeat: repeat-x;
    background-position-x: 45%;
    border-radius: 10px;
    background-position-y: bottom;
}

.p-body{
    border-radius: 5px;
    overflow-y: auto;
}

.overflow-scroll{
    overflow:  auto;
}

.p-link{
    text-decoration: none;
    padding: 3px 5px;
    color: white;
    border-radius: 7px;
    background-color: #3ab047;
    font-weight: 600;
}

.image-blob{
    height: fit-content;
    animation: fadeIn 1s forwards;
}

.fadeIn{
    animation: fadeIn .5s forwards;
}

.swipe{
    animation: swipe .3s forwards;
}

.swipe-exit{
    display: none !important;
}

@keyframes fadeIn{
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

.linkedin:{
    transition: .4s;
}

.linkedin:hover, .linkedin:active, .linkedin:focus {
    background: white;
    color: #3b64ae;
    transition: .4s;
}
.github:{
    transition: .4s;
}

.github:hover, .github:active, .github:focus {
    background: #181228;
    color: #eee;
    transition: .4s;
}

.gmail:{
    transition: .4s;
}

.gmail:hover, .gmail:active, .gmail:focus {
    background: white;
    color: red;
    transition: .4s;
}

.twitter:{
    transition: .4s;
}

.twitter:hover, .twitter:active, .twitter:focus {
    background: cornflowerblue;
    color: white;
    transition: .4s;
}


.nav{
    position: fixed;
    top: 0px;
    z-index: 100;
    width: 100%;
    left: 0px;
    background: transparent;
}
.nav__list{
    background: transparent;
    justify-content: center;
}

.page{
    padding: 50px 0px;
}

@media screen and (max-width: 470px){
    .image-blob{
        display: none;
    }
}

@media screen and (max-width: 698px){
    .nav{
        position: fixed;
        bottom: 0px;
        top: unset;
    }

    .page{
        padding: 0px 0px 60px 0px;
    }

    .nav__list{
        background: #dedeea;
        /*background: initial;*/
        justify-content: center;
    }

    .nav__link{
        display: block;
        text-align: center;
    }

    .nav__link h3{
        display: none;
    }

    .p-header{
        background-size: cover !important;

    }
}


@keyframes rightSlide{
    from{
        transform: translateX(150%);
    }
    to{
        transform: translateX(0%);

    }
}